body {
  @apply bg-dark-blue;
}

#login {
  width: 370px;
}
#login #login_error {
  color: #000;
}
#login .message {
  color: #000;
}
#login h1 a {
  width: 100px;
  height: 100px;
  background-size: 100px 100px;
  margin-bottom: 40px;
}
#login h1 a:focus {
  box-shadow: none;
}
#login form {
  @apply shadow-none p-10 border-white/0 bg-white mt-0;
}
#login form label {
  @apply font-transducer uppercase text-xs mb-2 tracking-wide;
}
#login form input#user_pass,
#login form input#user_login {
  @apply rounded-none w-full;
}
#login form input#user_pass:focus-visible,
#login form input#user_login:focus-visible {
  @apply outline-blue;
}
#login form input[type=submit] {
  @apply inline-block font-transducer font-normal text-sm leading-[18px] tracking-[0.08em] border border-blue text-blue uppercase py-[10px] px-[22px] transition-all duration-300 ease-in-out hover:bg-blue hover:text-white bg-white rounded-none;
}
#login #backtoblog, #login #nav {
  @apply font-transducer text-center;
}
#login #backtoblog a, #login #nav a {
  @apply font-transducer;
}
#login input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 30px white inset;
  -webkit-text-fill-color: #000 !important;
}

.wpml-login-ls form#wpml-login-ls-form select#wpml-language-switcher-locales {
  @apply rounded-none border border-solid uppercase text-xs font-transducer transition-all duration-300 ease-in hover:bg-white;
}
.wpml-login-ls form#wpml-login-ls-form input.button {
  @apply rounded-none border border-solid uppercase text-xs font-transducer px-4 py-1 transition-all duration-300 ease-in hover:bg-white;
}